import {
    REGISTER_START,
    REGISTER_ERROR,
    REGISTER_SUCCESS,
    MESSAGE_START,
    MESSAGE_ERROR,
    MESSAGE_SUCCESS
  } from '../actions/action-types';

  const initialState = { 
      loading: false, 
      err: null,
      username: null,
      chatHistory: []
  };

  export default (state = initialState, { type, payload }) => {
    switch (type) {
     case REGISTER_SUCCESS:
        return Object.assign({}, initialState, {
            chatHistory: payload.history.slice(),
            username: payload.username
        });
      case REGISTER_SUCCESS:
        return Object.assign({}, initialState, {
            chatHistory: payload.history.slice(),
            username: payload.username
        });
      case MESSAGE_SUCCESS:
        return {
            ...state,
            chatHistory: [...state.chatHistory, payload],
        }
      case REGISTER_START:
      case MESSAGE_START: 
        return {
          ...state, loading: true
        };
      case REGISTER_ERROR:
      case MESSAGE_ERROR:
        return {
            ...state, 
            err: payload.err, 
            loading: false
        };
      default:
        return state;
    }
  };
  


