import {
  REGISTER,
  MESSAGE,
} from './action-types';

export const register = (username) => {
  return {
    type: REGISTER,
    payload: username
  }
};

export const message = (message) => {
  return dispatch => {
    dispatch(sendMessageAsync(message))
  };
};

const registerUsernameAsync = (username) => {
  return {
    type: REGISTER,
    payload: username
  }
}

const sendMessageAsync = (message) => {
  return {
    type: MESSAGE,
    payload: message
  }
}



