import io from 'socket.io-client' 
import { push } from 'connected-react-router'
import _ from 'lodash';
import {
    REGISTER,
    REGISTER_START,
    REGISTER_ERROR,
    REGISTER_SUCCESS,
    MESSAGE,
    MESSAGE_START,
    MESSAGE_ERROR,
    MESSAGE_SUCCESS
  } from './actions/action-types';
  
let socket;
export const createSocketMiddleWare = store => {

    if (!socket) {
        socket = io("http://localhost:4000");

        socket.on("message", (message) => {
            store.dispatch({
                type : MESSAGE_SUCCESS,
                payload : message
            });
        });
    }

    return next => action => {
        switch(action.type) {
            case REGISTER : {
                store.dispatch({
                    type: REGISTER_START
                })    

                socket.emit(REGISTER, action.payload, (err, chatHistory) => {
                    if (err) {
                        store.dispatch({
                            type: REGISTER_ERROR,
                            payload : {err: err}
                        })
                    }
                    else {
                        store.dispatch({
                            type: REGISTER_SUCCESS,
                            payload : {
                                username: action.payload,
                                history: chatHistory
                            }
                        })

                        store.dispatch(push('/chat'))
                    }
                })
                
                return;
            }

            case MESSAGE: {
                store.dispatch({
                    type: MESSAGE_START
                })  

                socket.send({...action.payload, cb: (err) => {
                    if (err)
                        store.dispatch({
                            type: MESSAGE_ERROR,
                            payload : {err: err}
                        })
                }})

                return;
            }
        }

        return next(action);
    }
}


