import React, {Component} from 'react'
import {connect} from 'react-redux';  
import {bindActionCreators} from 'redux';
import { Input, Button } from "reactstrap";
import {getChatState} from '../selectors'
import Spinner from './Spinner'

class Chat extends Component {
  constructor() {
    super();
    this.state = {
      message: '',
    };
  }

  onChange = (event) => {
      this.setState({message: event.target.value})
  }

  onSend = (event) => {
    this.setState({message: ''})
  }

  render() {
    return (
      <div className="layout layout-center">
          <header className="page-title pt-5">
              <h1>Chat {this.props.chat.username}</h1>
          </header>
          <main className="flex row wrap">
              <ul className="chat-list">
                {
                  this.props.chat.history.map((val, idx) => {
                    return <li key={idx}>{val}</li>
                  })
                }
              </ul>
              <Input className="chat-message"
                  value={this.state.message}
                  onChange={this.onChange}
              />
              <Button 
                  disabled={!this.state.message || this.props.chat.loading} 
                  className="pl-1 chat-button"
                  onClick={this.onSend}> 
                  Send 
              </Button>
          </main>
      </div>
    )
  }
}

  
const mapStateToProps = state => {
    return {
      chat: getChatState(state)
    }
  };
  
export default connect(mapStateToProps, null)(Chat)


